* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: white;
}

.header {
  background-color: rgb(237, 186, 90);
  display: flex;
  color: white;
  align-items: center;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  padding: 10px 15px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

footer {
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 48vw;
  margin-bottom: 5px;
}

.main_note {
  max-height: 200px;
  width: 350px;
  margin: auto;
  margin-top: 30px;
  margin-bottom: 30px;
  padding: 10px;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 5px;
  position: relative;
}

.main_note input {
  width: 280px;
  height: 40px;
  font-size: 20px;
  color: black;
  border: none;
  outline: none;
}

.main_note textarea {
  max-width: 330px;
  max-height: 50px;
  font-size: 18px;
  color: black;
  border: none;
  outline: none;
  margin-bottom: 30px;
}

.main_note .btn {
  height: 50px;
  width: 50px;
  transform: scale(0.8);
  border-radius: 50px;
  background-color: white;
  color: rgb(237, 186, 90);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  cursor: pointer;
  position: absolute;
  right: 0;
  bottom: -23px;
}

.main_note .btn:hover {
  color: white;
  background-color: rgb(237, 186, 90);
}

.note {
  max-height: 500px;
  width: 230px;
  margin: 20px;
  padding: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 5px;
  position: relative;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;


}

.note p{
  font-size: 18px;
  margin-bottom: 25px;
}

.deleteIcon{
  position: absolute;
  bottom: 15px;
  right: 15px;
  height: 50px;
  width: 50px;
  transform: scale(1.3);
  border-radius: 50px;
  background-color: white;
  color: rgb(237, 186, 90);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  cursor: pointer;
}

.deleteIcon:hover{
  color: white;
  background-color: rgb(237, 186, 90);
}

.note_container{
  display: inline-flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-bottom: 30px;
}